import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddS1ApplicationComponent } from './add-s1-application.component';

const routes: Routes = [
  {
    path: '',
    component: AddS1ApplicationComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddS1ApplicationRoutingModule { }
